.home-about {
  margin-top: 4 * $base-spacing;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  background-image: url("data:image/svg+xml;base64,");
  background-size: contain;
  background-position: right;
  background-repeat: no-repeat;
  transform-style: preserve-3d;

  &::after {
    display: block;
    content: "";
    position: absolute;
    width: calc(200vw + 100vh - #{4 * $base-spacing});
    height: 100%;
    left: 0;
    z-index: -9;
    background-color: $vanilla;
    transition: transform 3 * $base-duration $base-timing, opacity 3 * $base-duration $base-timing;
    transform: translateX(0);
    opacity: 0;
    background-image: linear-gradient(135deg, white 50%, $vanilla 50%);
  }

  &::before {
    content: "";
    height: 50%;
    position: absolute;
    top: 55%;
    right: 5vw;
    width: 60vw;
    background-image: radial-gradient(#fff 20%, transparent 20%);
    background-position: 10px 10px;
    background-size: 7px 7px;

    @media screen and (min-width: $medium-screen) {
      will-change: transform;
      transform-style: preserve-3d;
      transform: translate3d(0, 0, 0.25px) scale(0.75);
    }
  }

  .container {
    transform-style: preserve-3d;
    // position: relative;
  }

  &--text {
    position: relative;
    z-index: 9;
    margin-top: 8 * $base-spacing;
    margin-bottom: 4 * $base-spacing;
    opacity: 0;
    transition: opacity ($base-duration * 3) $base-duration $base-timing;

    @media screen and (min-width: $medium-screen) {
      will-change: transform;
      transform-style: preserve-3d;
      perspective-origin: center center;
      transform: translate3d(0, 0, 0.2px) scale(0.8);
    }
  }

  &--title {
    font-size: modular-scale(6);
    font-weight: 400;
    color: white;
    width: 70%;
    margin-bottom: $base-spacing;
  }

  &--image {
    position: absolute;
    right: -2 * $base-spacing;
    bottom: -2 * $base-spacing;
    width: 55%;
    height: auto;

    @media screen and(min-width: $medium-screen) {
      will-change: transform;
      transform-style: preserve-3d;
      transform: translate3d(0, 0, 0.1px) scale(0.9);
    }
  }

  &.in-view {
    &::after {
      transform: translateX(calc(#{4 * $base-spacing} - 100vw - 100vh));
      opacity: 1;
    }

    .home-about--text {
      opacity: 1;
    }
  }
}



